@tailwind base;
@tailwind components;
@tailwind utilities;

/* Градиентный анимированный фон */
.animated-gradient {
  background: linear-gradient(45deg, #ff9a9e, #fad0c4, #ffdde1);
  background-size: 400% 400%;
  animation: gradientAnimation 15s ease infinite;
}

@keyframes gradientAnimation {
  0% {
    background-position: 0% 50%;
  }
  50% {
    background-position: 100% 50%;
  }
  100% {
    background-position: 0% 50%;
  }
}

/* Эффект рассеивания */
@keyframes scatter {
  0% {
    transform: translate(-50%, -50%) scale(1);
    opacity: 0.8;
  }
  100% {
    transform: translate(calc(-50% + var(--offset-x)), calc(-50% + var(--offset-y))) scale(0.5);
    opacity: 0;
  }
}
.animate-scatter {
  animation: scatter 0.6s forwards;
}

/* Поп эффект */
@keyframes pop {
  0% {
    transform: scale(0);
    opacity: 0;
  }
  60% {
    transform: scale(1.1);
    opacity: 1;
  }
  100% {
    transform: scale(1);
    opacity: 1;
  }
}
.animate-pop {
  animation: pop 0.5s ease-out forwards;
}

/* Поп-оут */
@keyframes pop-out {
  0% {
    transform: scale(1);
    opacity: 1;
  }
  100% {
    transform: scale(0);
    opacity: 0;
  }
}
.animate-pop-out {
  animation: pop-out 0.5s ease-in forwards;
}

/* Эффект нажатия */
@keyframes pressEffect {
  0% {
    transform: scale(0.8);
    opacity: 0.7;
  }
  50% {
    transform: scale(1.2);
    opacity: 0.3;
  }
  100% {
    transform: scale(1.5);
    opacity: 0;
  }
}
.animate-press {
  animation: pressEffect 0.4s ease-out forwards;
}

/* Анимация плавного движения вверх/в сторону */
@keyframes float {
  0% {
    opacity: 1;
    transform: translate(0, 0) scale(1);
  }
  100% {
    opacity: 0;
    transform: translate(var(--final-x), var(--final-y)) scale(1.5);
  }
}
.animate-float {
  animation: float 1s ease-out forwards;
}

@keyframes raindrop {
  0% {
    transform: translateY(-100px);
    opacity: 0;
  }
  10% {
    opacity: 0.4;
  }
  90% {
    opacity: 0.4;
  }
  100% {
    transform: translateY(800vh);
    opacity: 0;
  }
}

.animate-raindrop {
  animation-name: raindrop;
  animation-timing-function: linear;
  animation-fill-mode: forwards;
  animation-iteration-count: infinite;
}

.carousel-container {
  position: relative;
  max-width: 320px; /* фиксируем ширину */
  margin: 0 auto;
  overflow: hidden;
  border-radius: 1rem;
}

.carousel-track {
  display: flex;
  transition: transform 0.5s ease-in-out;
}

.carousel-item {
  flex-shrink: 0;
  width: 100%;
  box-sizing: border-box;
}

