/* effects.css */

/* Эффект ripple – волна при нажатии */
@keyframes ripple {
  0% {
    opacity: 0.4;
    transform: scale(1);
  }
  100% {
    opacity: 0;
    transform: scale(1.8);
  }
}

.animate-ripple {
  animation: ripple 0.6s ease-out;
}

/* Эффект scatter – разлетающиеся частицы */
@keyframes scatter {
  0% {
    transform: translate(-50%, -50%) scale(1);
    opacity: 0.8;
  }
  100% {
    transform: translate(
      calc(-50% + var(--offset-x)),
      calc(-50% + var(--offset-y))
    ) scale(0.5);
    opacity: 0;
  }
}

.animate-scatter {
  animation: scatter 0.6s forwards;
}

/* Эффект падения для декоративных элементов (если нужен) */
@keyframes fall {
  0% {
    transform: translateY(0);
    opacity: 0.2;
  }
  20% {
    opacity: 0.4;
  }
  100% {
    transform: translateY(110vh);
    opacity: 0;
  }
}

.animate-fall {
  animation: fall 25s linear infinite;
}
